@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comfortaa&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Francois+One&display=swap');


*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-image: linear-gradient( rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7) ), url('images/bg.jpg');
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}


.navContainer{
  width: 100%;
  height: 100px;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btns{
  width: 70%;
  height: 60%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.home, .walibtn, .Photos{
  color: white;
  text-decoration: none;
  font-size: 18px;
}





.header{
  font-size: 80px;
  font-weight: bold;
  font-family: 'Fredoka One', cursive;
  letter-spacing: 7px;
  text-align: center;
  color: rgb(255,255,255,0.7);

  position: absolute;
  top: 70px;
  left: 50%;
  transform: translateX(-50%);
  transition: all ease-in-out 250ms;
  z-index: 0;
}

.num{
  font-size: 300px;
  color: #4DC247;
  opacity: 0.5;
  position: relative;
  top: 60px;
  transition: all ease-in-out 250ms;
}

.tagline{
  font-size: 45px;
}

.num:hover{
  font-size: 302px;
  opacity: 1;
}

.MIA1:hover{
  font-size: 85px;
  color:rgb(255,255,255, 1);
}

.tagline:hover{
  font-size: 47px;
  color:rgb(255,255,255, 1);
}










.Container{
  position: absolute;
  top: 0;
  background: #14171A;
  width: 100%;
  height: 100vh;
  z-index: -1;
}

.wali{
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 50%;
  height: 400px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.picwali{
  margin-bottom: 10px;
  width: 200px;
  border-radius: 50px;
  border: 2px solid darkgrey;
}

.namaWali{
  font-size: 30px;
  font-family: 'Comfortaa', cursive;
  color: white;
}

.descWali{
  font-size: 25px;
  font-family: 'Comfortaa', cursive;
  color: white;
}

.namaWali, .descWali{
  position: relative;
  top: 25px;
}

.gif1{
  position: absolute;
  top: 50%;
  left: 0%;
  width: 150px;
}

.gif2{
  position: absolute;
  top: 30%;
  left: 90%;
  width: 150px;
}

.gif3{
  position: absolute;
  top: -10%;
  left: -10%;
  width: 150px;
}
